@import url(https://cdn.jsdelivr.net/npm/bulma@0.9.4/css/bulma.min.css);
* {
  border-radius: 5px;
}

.App {
  margin: 0 auto;
  width: 600px;
}

form {
  padding: 20px;
  display: flex;
  flex-direction: column;
  background-color: lavenderblush;
  margin: 20px 0;
}

input,
select {
  font-size: 18px;
  width: 100%;
  margin-bottom: 20px;
}

li {
  border: 2px solid aqua;
}

li:not(:last-child) {
  margin-bottom: 15px;
}

.todo__title{font-weight:600;font-size:20px;margin:5px 20px;text-align:center}.todo__status{margin:5px 20px}.todo__notCompleated{display:inline;color:red}.todo__compleated{color:green}

.user{display:flex;justify-content:space-around;border-top:1px solid black;border-radius:0;margin-top:10px}

